/**
 * Clear all client-side cache including:
 * - localStorage
 * - IndexedDB
 * - Service Worker cache (if available)
 */
export async function clearAllCache(): Promise<void> {
  try {
    // Clear localStorage (except for essential items like customer email)
    const essentialKeys = ["leenk_customer_email"] // Keep customer email
    const keysToRemove: string[] = []
    
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i)
      if (key && !essentialKeys.includes(key)) {
        keysToRemove.push(key)
      }
    }
    
    keysToRemove.forEach(key => localStorage.removeItem(key))
    
    // Clear IndexedDB
    if (typeof window !== "undefined" && "indexedDB" in window) {
      try {
        // Clear message cache
        const messageDbRequest = indexedDB.deleteDatabase("messageCache")
        await new Promise((resolve, reject) => {
          messageDbRequest.onsuccess = () => resolve(undefined)
          messageDbRequest.onerror = () => reject(messageDbRequest.error)
          messageDbRequest.onblocked = () => {
            // If blocked, try again after a short delay
            setTimeout(() => resolve(undefined), 100)
          }
        })
        
        // Clear conversation cache
        const conversationDbRequest = indexedDB.deleteDatabase("conversationCache")
        await new Promise((resolve, reject) => {
          conversationDbRequest.onsuccess = () => resolve(undefined)
          conversationDbRequest.onerror = () => reject(conversationDbRequest.error)
          conversationDbRequest.onblocked = () => {
            setTimeout(() => resolve(undefined), 100)
          }
        })
      } catch (error) {
        console.warn("Error clearing IndexedDB:", error)
        // Continue even if IndexedDB clearing fails
      }
    }
    
    // Clear Service Worker cache if available
    if (typeof window !== "undefined" && "serviceWorker" in navigator && "caches" in window) {
      try {
        const cacheNames = await caches.keys()
        await Promise.all(
          cacheNames.map(cacheName => caches.delete(cacheName))
        )
      } catch (error) {
        console.warn("Error clearing Service Worker cache:", error)
        // Continue even if cache clearing fails
      }
    }
    
    // Force a hard reload to ensure everything is cleared
    if (typeof window !== "undefined") {
      window.location.reload()
    }
  } catch (error) {
    console.error("Error clearing cache:", error)
    throw error
  }
}

/**
 * Check if cache clearing is supported
 */
export function isCacheClearingSupported(): boolean {
  return typeof window !== "undefined" && (
    typeof Storage !== "undefined" ||
    "indexedDB" in window ||
    ("serviceWorker" in navigator && "caches" in window)
  )
}

